import {
  Button,
  Modal,
  ModalBody,
  ModalFooter,
  ModalTitle,
} from 'react-bootstrap'
import cameraIcon from '../icons/camera.svg'
import React, { useState } from 'react'
import ModalHeader from 'react-bootstrap/ModalHeader'
import { sendClick } from '../Hooks/useAnalytics'

const PhotoContainer = ({ photoAlbumSet }) => {
  const [showPhotoLibrary, setShowPhotoLibrary] = useState(false)
  const [photo, setPhoto] = useState(undefined)

  const handleShowPhotoLibrary = (value) => (e) => {
    sendClick('photo-picker-activated', null, 0 + value)
    setShowPhotoLibrary(value)
  }

  const handlePhotoClickFactory = (image, i) => (e) => {
    sendClick('photo-selected', null, i)
    setPhoto(image)
    setShowPhotoLibrary(false)
  }

  return (
    <>
      <Modal show={showPhotoLibrary} onHide={handleShowPhotoLibrary(false)}>
        <ModalHeader closeButton>
          <ModalTitle>Select a photo from your photo library</ModalTitle>
        </ModalHeader>
        <ModalBody>
          <div className="photo-album-thumbnails">
            {photoAlbumSet.map((image, i) => (
              <div className="thumbnail-container">
                <img
                  src={image}
                  onClick={handlePhotoClickFactory(image, i)}
                  className="photo-album-thumbnail"
                  alt=""
                />
              </div>
            ))}
          </div>
        </ModalBody>
        <ModalFooter>
          <Button variant="primary" onClick={handleShowPhotoLibrary(false)}>
            Close
          </Button>
        </ModalFooter>
      </Modal>
      <div className="photo-container">
        {photo && <img src={photo} alt="" />}
        <div className="buttons">
          <Button
            onClick={handleShowPhotoLibrary(true)}
            size="sm"
            variant="light">
            <img
              className="btn-xs-icon"
              src={cameraIcon}
              alt="Show photo album"
            />
          </Button>
        </div>
      </div>
    </>
  )
}

export default PhotoContainer
