import * as PropTypes from 'prop-types'
import React from 'react'
import avatar from '../icons/owlfred.svg'

function OwlSay({ wide, children }) {
    const showWide = children.length > 170 || wide
    return (
        <div className="OwlfredAgent">
            <img className="owlfred" src={avatar} alt="Helpful owl" />
            <span className={`bubble ${showWide && 'bubble-wide'}`}>
                {children}
            </span>
        </div>
    )
}
OwlSay.propTypes = {
    wide: PropTypes.bool,
}

export default OwlSay
